"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Upload, CheckCircle, Loader2, Download, RotateCw, AlertCircle } from "lucide-react";
import jsPDF from "jspdf";
import { Collapsible, CollapsibleContent, CollapsibleTrigger } from "@/components/ui/collapsible";
import { ChevronDown, ChevronUp } from "lucide-react";

const DashboardPage = () => {
  const [file, setFile] = useState(null);
  const [isDragging, setIsDragging] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [reportGenerated, setReportGenerated] = useState(false);
  const [reportContent, setReportContent] = useState("");
  const [backendResponse, setBackendResponse] = useState(null);
  const [error, setError] = useState(null);

  const handleFileChange = (event) => {
    const selectedFile = event.target.files?.[0];
    if (
      selectedFile &&
      (selectedFile.type === "application/pdf" ||
        selectedFile.type === "application/vnd.openxmlformats-officedocument.wordprocessingml.document" ||
        selectedFile.type === "application/msword")
    ) {
      setFile(selectedFile);
      setError(null);
    } else {
      setError("Please upload a valid PDF or DOC file.");
      setFile(null);
    }
  };

  const handleDragOver = (event) => {
    event.preventDefault();
    setIsDragging(true);
  };

  const handleDragLeave = () => {
    setIsDragging(false);
  };

  const handleDrop = (event) => {
    event.preventDefault();
    setIsDragging(false);
    const droppedFile = event.dataTransfer.files?.[0];
    if (
      droppedFile &&
      (droppedFile.type === "application/pdf" ||
        droppedFile.type === "application/vnd.openxmlformats-officedocument.wordprocessingml.document" ||
        droppedFile.type === "application/msword")
    ) {
      setFile(droppedFile);
      setError(null);
    } else {
      setError("Please drop a valid PDF or DOC file.");
      setFile(null);
    }
  };

  const generateImprovedReport = async (data) => {
    const doc = new jsPDF();
    const { syllabus, enhancement } = data;
    const fileName = file?.name || "Unknown File";

    try {
      // Load the logo
      const img = new Image();
      img.src = "/esprit.png";

      const base64 = await new Promise((resolve) => {
        img.onload = () => {
          const canvas = document.createElement("canvas");
          canvas.width = img.width;
          canvas.height = img.height;
          const ctx = canvas.getContext("2d");
          ctx.drawImage(img, 0, 0);
          const dataURL = canvas.toDataURL("image/png");
          resolve(dataURL);
        };
        img.onerror = () => resolve(null);
      });

      if (base64) {
        doc.addImage(base64, "PNG", 10, 10, 30, 30);
      } else {
        doc.setFontSize(16);
        doc.text("Syllabus Improvement Report", 50, 25);
      }

      let y = 50;
      const pageWidth = doc.internal.pageSize.getWidth();
      const margin = 20;
      const maxWidth = pageWidth - margin * 2;

      doc.setFontSize(24);
      doc.setTextColor(120, 0, 0);
      doc.text(`Syllabus Improvement: ${fileName}`, margin, y);
      y += 30;

      doc.setFontSize(12);
      doc.setTextColor(0, 0, 0);

      const addText = (text, x, yPos, options = {}) => {
        const { maxLines = 30, fontSize = 12, isListItem = false } = options;
        doc.setFontSize(fontSize);
        const splitText = doc.splitTextToSize(text, maxWidth - (isListItem ? 15 : 0));

        for (let i = 0; i < splitText.length; i++) {
          if (yPos > doc.internal.pageSize.getHeight() - 20) {
            doc.addPage();
            yPos = 20;
          }
          doc.text(
            (isListItem ? (i === 0 ? "- " : "  ") : "") + splitText[i],
            x + (isListItem && i > 0 ? 15 : 0),
            yPos
          );
          yPos += 7;
        }
        return yPos + 5;
      };

      y = addText("Original Learning Outcomes:", margin, y, { fontSize: 16 });
      const originalOutcomes = syllabus?.content?.objectives || [];
      if (originalOutcomes.length === 0) {
        y = addText("No objectives provided in the syllabus.", margin + 5, y);
      } else {
        originalOutcomes.forEach((item) => {
          y = addText(item, margin + 5, y, { isListItem: true });
        });
      }

      y = addText("Improved Learning Outcomes:", margin, y, { fontSize: 16 });
      const improvedOutcomes = enhancement?.improved_outcomes || [];
      if (improvedOutcomes.length === 0) {
        y = addText("No improved outcomes generated.", margin + 5, y);
      } else {
        improvedOutcomes.forEach((item) => {
          y = addText(item, margin + 5, y, { isListItem: true });
        });
      }

      y = addText("Assessment Improvements:", margin, y, { fontSize: 16 });
      y = addText("Bloom's Taxonomy Analysis", margin + 5, y, { fontSize: 14 });
      y = addText(
        `Found Levels: ${enhancement?.bloom_analysis?.found_levels?.join(", ") || "N/A"}`,
        margin + 10,
        y
      );
      y = addText(
        `Missing Levels: ${enhancement?.bloom_analysis?.missing_levels?.join(", ") || "N/A"}`,
        margin + 10,
        y
      );

      y = addText("Misalignments:", margin, y, { fontSize: 14 });
      const misalignments = enhancement?.misalignments || [];
      if (misalignments.length === 0) {
        y = addText("No misalignments detected.", margin + 10, y);
      } else {
        misalignments.forEach((item) => {
          y = addText(item, margin + 10, y, { isListItem: true });
        });
      }

      y = addText("Suggestions:", margin, y, { fontSize: 14 });
      const suggestions = enhancement?.suggestions || [];
      if (suggestions.length === 0) {
        y = addText("No suggestions provided.", margin + 10, y);
      } else {
        suggestions.forEach((item) => {
          y = addText(item, margin + 10, y, { isListItem: true });
        });
      }

      y = addText("Scoring:", margin, y, { fontSize: 14 });
      y = addText(`Score: ${enhancement?.scoring?.score || "N/A"}/100`, margin + 10, y);
      y = addText(
        `Bloom Coverage: ${enhancement?.scoring?.bloom_coverage || "N/A"}%`,
        margin + 10,
        y
      );
      y = addText(
        `Alignment Score: ${enhancement?.scoring?.alignment_score || "N/A"}`,
        margin + 10,
        y
      );
      y = addText(
        `Completeness Score: ${enhancement?.scoring?.completeness_score || "N/A"}`,
        margin + 10,
        y
      );

      const content = `
Syllabus Improvement Report

File Processed: ${fileName}

Original Learning Outcomes:
${originalOutcomes.length === 0 ? "No objectives provided in the syllabus." : originalOutcomes.map((item) => `- ${item}`).join("\n")}

Improved Learning Outcomes:
${improvedOutcomes.length === 0 ? "No improved outcomes generated." : improvedOutcomes.map((item) => `- ${item}`).join("\n")}

Assessment Improvements:
Bloom's Taxonomy Analysis
- Found Levels: ${enhancement?.bloom_analysis?.found_levels?.join(", ") || "N/A"}
- Missing Levels: ${enhancement?.bloom_analysis?.missing_levels?.join(", ") || "N/A"}

Misalignments:
${misalignments.length === 0 ? "No misalignments detected." : misalignments.map((item) => `- ${item}`).join("\n")}

Suggestions:
${suggestions.length === 0 ? "No suggestions provided." : suggestions.map((item) => `- ${item}`).join("\n")}

Scoring:
- Score: ${enhancement?.scoring?.score || "N/A"}/100
- Bloom Coverage: ${enhancement?.scoring?.bloom_coverage || "N/A"}%
- Alignment Score: ${enhancement?.scoring?.alignment_score || "N/A"}
- Completeness Score: ${enhancement?.scoring?.completeness_score || "N/A"}
      `;

      setReportContent(content.trim());
      return doc;
    } catch (error) {
      console.error("Error generating report:", error);
      throw error;
    }
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!file) {
      setError("Please select a file to upload.");
      return;
    }

    setIsLoading(true);
    setError(null);

    try {
      const formData = new FormData();
      formData.append("file", file);

      const response = await fetch("http://localhost:5000/api/syllabus/upload", {
        method: "POST",
        body: formData,
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      if (data.error) {
        if (data.error.includes("GROQ_API_KEY") || data.error.includes("Groq client")) {
          setError("The server is misconfigured. Please contact the administrator.");
        } else if (data.error.includes("No text")) {
          setError("The uploaded file contains no readable text. Please upload a valid syllabus file.");
        } else {
          setError(data.error || "An error occurred while processing the file.");
        }
        throw new Error(data.error);
      }

      setBackendResponse(data);
      await generateImprovedReport(data);
      setReportGenerated(true);
    } catch (error) {
      console.error("Error during file upload:", error);
      setError("Failed to process the file. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleDownload = async () => {
    if (!backendResponse) {
      setError("No report data available to download.");
      return;
    }

    try {
      const pdf = await generateImprovedReport(backendResponse);
      const fileName = file?.name
        ? `syllabus_improvement_${file.name.replace(/\.[^/.]+$/, "")}.pdf`
        : "syllabus_improvement_report.pdf";
      pdf.save(fileName);
    } catch (error) {
      setError("Failed to download report. Please try again.");
      console.error(error);
    }
  };

  const handleReset = () => {
    if (window.confirm("Are you sure you want to reset? All current data will be lost.")) {
      setFile(null);
      setReportGenerated(false);
      setReportContent("");
      setBackendResponse(null);
      setError(null);
    }
  };

  return (
    <div className="relative min-h-screen flex flex-col items-center justify-center overflow-hidden bg-gradient-to-br from-[#3b0000] via-[#4d0000] to-[#4e0000] px-4">
      <div className="relative z-10 w-full max-w-4xl flex flex-col items-center pt-2 pb-10 space-y-12">
        <h1 className="text-5xl font-extrabold leading-tight text-white drop-shadow-lg text-center">
          <span className="text-white">Syllabus</span> <span className="text-red-300">Improvement</span>
        </h1>

        <Card className="w-full bg-white/80 dark:bg-slate-900/80 backdrop-blur-md border border-gray-300 dark:border-gray-700 shadow-lg rounded-2xl">
          <CardHeader className="pb-2">
            <p className="text-center text-gray-800 dark:text-gray-200 text-lg font-medium">
              Upload your syllabus in PDF or DOC format
            </p>
          </CardHeader>

          <CardContent className="p-8">
            {error && (
              <div className="flex items-center space-x-2 p-4 mb-4 bg-red-100 dark:bg-red-900/30 rounded-lg">
                <AlertCircle className="w-5 h-5 text-red-500" />
                <p className="text-sm text-red-700 dark:text-red-300">{error}</p>
              </div>
            )}

            {!reportGenerated ? (
              <form onSubmit={handleSubmit} className="space-y-8">
                <div
                  className={`flex flex-col items-center justify-center w-full h-72 border-2 border-dashed rounded-xl cursor-pointer transition-colors duration-200 ${
                    isDragging
                      ? "border-red-400 bg-red-100 dark:bg-red-900/30"
                      : "border-gray-300 dark:border-gray-600 hover:bg-gray-100 dark:hover:bg-slate-800"
                  }`}
                  onDragOver={handleDragOver}
                  onDragLeave={handleDragLeave}
                  onDrop={handleDrop}
                >
                  <label
                    htmlFor="file-upload"
                    className="flex flex-col items-center justify-center w-full h-full"
                    aria-label="Upload syllabus file"
                  >
                    <div className="flex flex-col items-center space-y-4">
                      {file ? (
                        <CheckCircle className="w-16 h-16 text-green-500" />
                      ) : (
                        <Upload className="w-16 h-16 text-gray-400 dark:text-gray-500" />
                      )}
                      <p className="text-lg font-medium text-gray-700 dark:text-gray-300 uppercase">
                        {file ? file.name : "Upload PDF or DOC file"}
                      </p>
                      <p className="text-sm text-gray-500 dark:text-gray-400">
                        Click to browse or drag and drop
                      </p>
                    </div>
                    <input
                      id="file-upload"
                      type="file"
                      accept=".pdf,.doc,.docx"
                      onChange={handleFileChange}
                      className="hidden"
                      aria-describedby="file-upload-help"
                    />
                    <span id="file-upload-help" className="sr-only">
                      Upload a PDF or DOC file containing your syllabus
                    </span>
                  </label>
                </div>

                <Button
                  type="submit"
                  disabled={!file || isLoading}
                  className="w-full bg-gradient-to-r from-red-700 to-red-500 hover:from-red-800 hover:to-red-600 text-white text-lg py-6 rounded-full shadow-2xl transition-transform transform hover:scale-105 uppercase flex items-center justify-center space-x-2 disabled:opacity-50"
                  aria-busy={isLoading}
                >
                  {isLoading ? (
                    <>
                      <Loader2 className="w-5 h-5 animate-spin" />
                      <span>Processing...</span>
                    </>
                  ) : (
                    <span>Analyze Syllabus</span>
                  )}
                </Button>
              </form>
            ) : (
              <div className="flex flex-col space-y-6">
                <div className="bg-gray-100 dark:bg-gray-800 p-6 rounded-lg overflow-y-auto max-h-[70vh]">
                  <h2 className="text-lg font-bold mb-4 text-gray-800 dark:text-gray-200">
                    Syllabus Improvement Report
                  </h2>
                  <p className="text-sm text-gray-600 dark:text-gray-400 mb-4">
                    <strong>File Processed:</strong> {file?.name || "Unknown File"}
                  </p>

                  <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-6">
                    <div className="bg-white dark:bg-gray-700 p-4 rounded-lg shadow">
                      <h3 className="text-md font-semibold mb-2 text-gray-800 dark:text-gray-200">
                        Original Learning Outcomes
                      </h3>
                      {backendResponse?.syllabus?.content?.objectives?.length === 0 ? (
                        <p className="text-sm text-gray-500 dark:text-gray-400">
                          No objectives provided in the syllabus.
                        </p>
                      ) : (
                        <ul className="list-disc pl-5 text-sm text-gray-700 dark:text-gray-300">
                          {backendResponse?.syllabus?.content?.objectives?.map((item, index) => (
                            <li key={index} className="mb-1">{item}</li>
                          ))}
                        </ul>
                      )}
                    </div>
                    <div className="bg-blue-50 dark:bg-blue-900/30 p-4 rounded-lg shadow">
                      <h3 className="text-md font-semibold mb-2 text-blue-800 dark:text-blue-200">
                        Improved Learning Outcomes
                      </h3>
                      {backendResponse?.enhancement?.improved_outcomes?.length === 0 ? (
                        <p className="text-sm text-blue-500 dark:text-blue-400">
                          No improved outcomes generated.
                        </p>
                      ) : (
                        <ul className="list-disc pl-5 text-sm text-blue-700 dark:text-blue-300">
                          {backendResponse?.enhancement?.improved_outcomes?.map((item, index) => (
                            <li key={index} className="mb-1">{item}</li>
                          ))}
                        </ul>
                      )}
                    </div>
                  </div>

                  <Collapsible>
                    <CollapsibleTrigger className="flex items-center justify-between w-full p-4 bg-gray-200 dark:bg-gray-600 rounded-lg">
                      <h3 className="text-md font-semibold text-gray-800 dark:text-gray-200">
                        Additional Analysis
                      </h3>
                      <ChevronDown className="w-5 h-5 text-gray-600 dark:text-gray-300" />
                    </CollapsibleTrigger>
                    <CollapsibleContent className="p-4">
                      <div className="space-y-6">
                        <div>
                          <h4 className="text-md font-semibold text-gray-800 dark:text-gray-200">
                            Assessment Improvements
                          </h4>
                          <p className="text-sm text-gray-600 dark:text-gray-400">
                            <strong>Bloom's Taxonomy Analysis</strong>
                          </p>
                          <ul className="list-disc pl-5 text-sm text-gray-700 dark:text-gray-300">
                            <li>
                              Found Levels:{" "}
                              {backendResponse?.enhancement?.bloom_analysis?.found_levels?.join(", ") || "N/A"}
                            </li>
                            <li>
                              Missing Levels:{" "}
                              {backendResponse?.enhancement?.bloom_analysis?.missing_levels?.join(", ") || "N/A"}
                            </li>
                          </ul>
                        </div>

                        <div>
                          <h4 className="text-md font-semibold text-gray-800 dark:text-gray-200">
                            Misalignments
                          </h4>
                          {backendResponse?.enhancement?.misalignments?.length === 0 ? (
                            <p className="text-sm text-gray-500 dark:text-gray-400">
                              No misalignments detected.
                            </p>
                          ) : (
                            <ul className="list-disc pl-5 text-sm text-gray-700 dark:text-gray-300">
                              {backendResponse?.enhancement?.misalignments?.map((item, index) => (
                                <li key={index} className="mb-1">{item}</li>
                              ))}
                            </ul>
                          )}
                        </div>

                        <div>
                          <h4 className="text-md font-semibold text-gray-800 dark:text-gray-200">
                            Suggestions
                          </h4>
                          {backendResponse?.enhancement?.suggestions?.length === 0 ? (
                            <p className="text-sm text-gray-500 dark:text-gray-400">
                              No suggestions provided.
                            </p>
                          ) : (
                            <ul className="list-disc pl-5 text-sm text-gray-700 dark:text-gray-300">
                              {backendResponse?.enhancement?.suggestions?.map((item, index) => (
                                <li key={index} className="mb-1">{item}</li>
                              ))}
                            </ul>
                          )}
                        </div>

                        <div>
                          <h4 className="text-md font-semibold text-gray-800 dark:text-gray-200">
                            Scoring
                          </h4>
                          <ul className="list-disc pl-5 text-sm text-gray-700 dark:text-gray-300">
                            <li>Score: {backendResponse?.enhancement?.scoring?.score || "N/A"}/100</li>
                            <li>
                              Bloom Coverage: {backendResponse?.enhancement?.scoring?.bloom_coverage || "N/A"}%
                            </li>
                            <li>
                              Alignment Score: {backendResponse?.enhancement?.scoring?.alignment_score || "N/A"}
                            </li>
                            <li>
                              Completeness Score:{" "}
                              {backendResponse?.enhancement?.scoring?.completeness_score || "N/A"}
                            </li>
                          </ul>
                        </div>
                      </div>
                    </CollapsibleContent>
                  </Collapsible>
                </div>

                <div className="flex space-x-4">
                  <Button
                    onClick={handleDownload}
                    className="flex-1 bg-green-600 hover:bg-green-700 text-white text-lg py-4 rounded-xl shadow-md flex items-center justify-center space-x-2"
                    aria-label="Download report as PDF"
                  >
                    <Download className="w-5 h-5" />
                    <span>Download PDF</span>
                  </Button>

                  <Button
                    onClick={handleReset}
                    variant="outline"
                    className="flex-1 border-red-500 text-red-500 hover:bg-red-100 dark:hover:bg-red-900 py-4 rounded-xl shadow-md flex items-center justify-center space-x-2"
                    aria-label="Reset form"
                  >
                    <RotateCw className="w-5 h-5" />
                    <span>Reset</span>
                  </Button>
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default DashboardPage;